
import Search from 'vux/src/components/search/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { mapState, mapGetters, mapActions } from 'vuex'
import { cloneObj, sessionUtil } from '@/utils/utils.js'
export default {
data() {
return {
flag: true,
search: '',
userId: '',
disabled: false,
qualityData: [],
pageNum: 1,
pageSize: 10 - 1,
isEnd: false,
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
pulldown: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放刷新',
loadingContent: '刷新中...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup: {
content: '上拉加载更多',
pullUpHeight: 50,
height: 40,
autoRefresh: false,
downContent: '释放加载更多',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
},
sqlTableName: 'U020_WX_KS'
}
},
components: {
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Scroller,
Spinner
},
computed: {
...mapState({
userUid: state => state.userUid
})
},
deactivated() {
this.$destroy(true)
},
created() {
this.userId = this.sessionUtil.getUserId()
},
mounted() {

},
activated() {
var type = this.util.getUrlParam('pType')
this.refresh()

this.$refs.scroller.reset()
},
methods: {
getDate(time) {
let vm = this
if (vm.util.isNotEmpty(time)) {
time = new Date(time)

let theDate =
time.getFullYear() +
'-' +
(time.getMonth() > 8
? time.getMonth() + 1
: '0' + (time.getMonth() + 1)) +
'-' +
(time.getDate() > 8 ? time.getDate() + 1 : '0' + (time.getDate() + 1))
return theDate
} else {
return ''
}
},
searchFunc() {
this.refresh()
},
showExam(item) {

this.$router.push({
name: 'examination',
query: {
pType: 'paper',
data: item
},
params: { data: item }
})












},
onButtonClick(type, Id, index) {
var _this = this
this.util.confirm('是否删除？', function() {

if (type == 'delete') {
var url = '/admin/appDefaultFormAction!removeData.action'
var params = {
ids: Id,
sqlTableName: _this.sqlTableName,
flag: 2
}
_this.util.post(url, params, function(res) {
_this.util.unmask()

_this.qualityData.splice(index, 1)
_this.util.alert('删除成功')
})
}
})
},
handleEvents(type) {

},
loadMore() {

if (this.flag) {
this.flag = false
this.querryData(1, this.pageNum)
}

setTimeout(() => {
this.$refs.scroller.donePullup()
}, 10)
},
refresh() {
this.pageNum = 1
this.querryData(0, this.pageNum)
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 0)
})
},
querryData(type, start) {
this.util.mask()
var obj = new Object()

obj['company'] = sessionUtil.getCompanyId()
var DepartId = this.sessionUtil.getDepartId()
var _this = this
var searchParams = {
projectName: _this.search
}
var userId = _this.sessionUtil.getUserId()
var sql =
'select a.* from U009_Quality_Inspection  a left join U_Project b on a.xmid=b.Id ' +
" where  (RIGHT('" +
DepartId +
"',6)  = RIGHT(a.subCompany,6) or '" +
DepartId +
"'  in(select DepartId from ORG_Depart where DepartType='dept')  or   CHARINDEX('" +
DepartId +
"',a.subCompany)>0) "
var params = {
operateType: 'queryDataByEncSql',
param: JSON.stringify(obj),
searchParams: JSON.stringify(searchParams),
querySql: _this.util._encode(sql),
orderByName: 'DocCreated DESC',
start: start,
end: start + _this.pageSize
}
this.util.post(
'/admin/appAgentAction!runAgent.action?agentId=Agent_Wechat_Examination&appId=WeChat&Type=gettestScore',
params,
function(res) {

_this.qualityData =
type == 1
? _this.qualityData.concat(res.data.data)
: cloneObj(res.data.data)
if (res.data.length < _this.pageSize) {
_this.changePullupStatus(false)
_this.isEnd = true
} else {
_this.changePullupStatus(true)
_this.isEnd = false
}
_this.flag = true
_this.pageNum += res.data.data.length
_this.util.unmask()
}
)
},
changePullupStatus(enabled) {
if (enabled) {
this.$refs.scroller.enablePullup()
this.pullupEnabled = true
} else {
this.$refs.scroller.disablePullup()
this.pullupEnabled = false
}
},
addBx() {
this.$router.push({
name: 'examineAdd',
query: { pType: 'addNew', Id: '' }
})
}
}
}



